body {
  font-size: 100%;
  margin: 0;
  padding: 0;
  Font-Family: 'Signika', Sans-Serif;
}

body, caption, th, td, input, textarea, select, option, legend, fieldset, h1, h2, h3, h4, h5, h6 {
  font-size-adjust: 0.5;
}
  
#page {
  font-size: 1em;
  line-height: 1.25;
}
  
h1, h2, h3, h4 {
  Font-Family: 'Anton', Sans-Serif;
  letter-spacing: 0.05em;
}

h1 {
  font-size: 2em;
  line-height: 1.25;
}

h2 {
  font-size: 1.625em;
  line-height: 1.15384615;
}

h3 {
  font-size: 1.375em;
  line-height: 1.13636364;
}

h4 {
  font-size: 1.125em;
  line-height: 1.11111111;
}
  
blockquote {
  font-size: 1.25em;
  line-height: 1.25;
}

img, embed, object, video {
  max-width: 100%;
}

label {
  margin-bottom: 0.5em;
  margin-left: 0.5em;
}

fieldset, textarea {
  border: 0.1em solid rgba(155, 155, 155, 0.774);
  border-radius: 0.5em;
  Font-Family: 'Signika', Sans-Serif;
}

select {
  border: 1em solid white; 
  padding: 1em;
  background: rgba(155, 155, 155, 0.1);
  margin: 0 0 1em 0;
  max-width: 500px;
  border: 0.1em solid rgba(155, 155, 155, 0.774);
  border-radius: 0.5em;
  Font-Family: 'Signika', Sans-Serif;
}

select:focus, textarea:focus {
  outline: 0;
}

@media (min-width: 43.75em) {
  body {
      font-size: 1em;
      line-height: 1.375;
  }

  h1 {
      font-size: 2.5em;
      line-height: 1.125;
  }

  h2 {
      font-size: 2em;
      line-height: 1.25;
  }    

  h3 {
      font-size: 1.5em;
      line-height: 1.25;
  }

  h4 {
      line-height: 1.22222222;
  }

  blockquote {
      font-size: 1.5em;
      line-height: 1.45833333;
  }
}

@media (min-width: 56.25em) {
  h1 {
      font-size: 3em;
      line-height: 1.05;
  }
  
  h2 {
      font-size: 2.25em;
      line-height: 1.25;
  }

  h3 {
    font-size: 1.75em;
    line-height: 1.25;
  }    
}


